<template>
  <el-input v-model="number" v-money="mask" maxlength="19"></el-input>
</template>

<script>
import { VMoney } from 'v-money'
import ElInput from 'element-ui/packages/input/src/input'

export default {
  name: 'ElInputCurrency',
  componentName: 'ElInputCurrency',
  directives: {
    money: VMoney
  },
  components: {
    ElInput
  },
  props: {
    value: {
      type: Number,
      required: true,
      default: 0
    },
    mask: {
      type: Object,
      default: () => {
        return {
          decimal: ',',
          thousands: '.',
          prefix: '',
          suffix: '',
          precision: 0,
          masked: false
        }
      }
    }
  },
  data () {
    return {
      number: this.value
    }
  },
  watch: {
    number: {
      handler: 'handlerWatchNumber',
      immediate: true
    }
  },
  methods: {
    handlerWatchNumber (newVal) {
      let value = newVal
      if (typeof value === 'string') {
        value = +value.replace(/\./gi, '')
      }
      this.$emit('input', value)
    }
  }
}
</script>

